import React, { useEffect, useState } from "react";
import { CgImage } from "react-icons/cg";
import { MdClose } from "react-icons/md";
import { connect } from "react-redux";
import Loading from "../../components/Loading/Loading";
import { GetDocTitle } from "../../interfaces/document_validation";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  CandidateDetailsInterface,
  CandidateDetailsInterface_old,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { checkIfImageExists } from "../../utils/functions";

interface FooterResponsePropInterface {
  docTitle: string;
  docSubTitle: string;
  docStatus: "waiting" | "approved" | "rejected";
}

const FooterResponse: React.FC<FooterResponsePropInterface> = ({
  docTitle,
  docSubTitle,
  docStatus,
}): JSX.Element => {
  return (
    <div className="w-full flex md:flex-col flex-col justify-between items-start px-5 pt-4 pb-8 gap-2">
      {/* left part */}
      <div className="flex items-center gap-4">
        {/* doc title & status */}
        <section>
          <h2 className="text-blue-600 font-semibold text-2xl">{docTitle}</h2>
          <h2 className="text-blue-600 font-semibold text-xl">{docSubTitle}</h2>
          {/* status */}
          <div className="flex items-center gap-1">
            <span className="text-italic text-gray-800">Status:</span>
            <span
              className={`text-${
                docStatus === "waiting"
                  ? "yellow"
                  : docStatus === "approved"
                  ? "blue"
                  : "gray"
              }-700`}
            >
              {docStatus === "waiting"
                ? "Pending"
                : docStatus === "approved"
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* end status */}
        </section>
        {/* end doc title & status */}
      </div>
      {/* end left part */}
    </div>
  );
};
interface FooterInitialPropInterface {
  doc: CandidateDetailsInterface;
  docStatus: "waiting" | "approved" | "rejected";
  setDoc: (data: CandidateDetailsInterface_old | null) => void;
  documentCategories: DocumentReducerInterface;
}

const FooterInitial: React.FC<FooterInitialPropInterface> = ({
  doc,
  docStatus,
  setDoc,
  documentCategories,
}): JSX.Element => {
  return (
    <div className="w-full flex md:flex-col flex-col justify-between items-start gap-2">
      {/* left part */}
      <div className="flex items-center gap-4">
        {/* doc title & status */}
        <section className="p-3">
          <div className="flex flex-col md:flex-row items-start md:items-center">
            <h2 className="text-gray-600 font-semibold text-xl">
              {GetDocTitle(documentCategories, doc).main}
            </h2>
          </div>
          {/* status */}
          <div className="flex items-center gap-1">
            <span className="text-italic text-gray-800">Status:</span>
            <span
              className={`text-${
                docStatus === "waiting"
                  ? "yellow"
                  : docStatus === "approved"
                  ? "blue"
                  : "gray"
              }-700`}
            >
              {docStatus === "waiting"
                ? "Pending"
                : docStatus === "approved"
                ? "Approved"
                : "Rejected"}
            </span>
          </div>
          {/* end status */}
        </section>
        {/* end doc title & status */}
      </div>
      {/* end left part */}

      {/* right part */}
      <div className="flex flex-row md:flex-row justify-end px-3 w-full items-center gap-2 overflow-x-auto">
        <button
          className="bg-gray-700 px-4 py-2 font-semibold text-white inline-flex items-center space-x-2 focus:outline-none"
          onClick={() => setDoc(null)}
        >
          <MdClose className="w-5 h-5" />
          <span>Close</span>
        </button>
      </div>
      {/* end right part */}
    </div>
  );
};

interface AppProps {
  doc: CandidateDetailsInterface_old;
  docTitle: string;
  docSubTitle: string;
  setDoc: (data: CandidateDetailsInterface_old | null) => void;
  documentCategories: DocumentReducerInterface;
}
const FileView: React.FC<AppProps> = ({
  doc,
  docTitle,
  docSubTitle,
  setDoc,
  documentCategories,
}) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [isImageValid, setIsImageValid] = useState<boolean | null>(null);

  useEffect(() => {
    if (isImageValid === null) {
      setLoading(true);
      checkIfImageExists(API.AUTH_FILES + doc.files, (result: boolean) =>
        setIsImageValid(result)
      );
    } else {
      setLoading(false);
    }
  }, [doc.files, isImageValid]);

  if (loading) {
    return <Loading />;
  }

  return (
    <div className="bg-gray-50 grid grid-cols-1 md:grid-cols-3">
      {/* User Access Select */}
      {/* image content */}
      {doc.files ? (
        <div className="col-span-2 max-h-full md:max-h-screen overflow-y-auto">
          {isImageValid ? (
            <img
              src={API.AUTH_FILES + doc.files}
              alt=""
              className="w-full animate__animated animate__zoomIn"
            />
          ) : (
            <div className="text-center text-red-400 py-8 animate__animated animate__zoomIn">
              <CgImage className="mx-auto w-40 h-40" />
              <span className="font-semibold text-4xl">Invalid Image</span>
            </div>
          )}
        </div>
      ) : (
        "No Files Provided"
      )}
      {/* end image content */}

      {/* footer */}
      <div>
        {doc.files ? (
          <FooterInitial
            doc={doc}
            docStatus={doc.validation_status}
            setDoc={setDoc}
            documentCategories={documentCategories}
          />
        ) : (
          <FooterResponse
            docTitle={docTitle}
            docSubTitle={docSubTitle}
            docStatus={doc.validation_status}
          />
        )}
      </div>
      {/* end footer */}
    </div>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  documentCategories: DocumentReducerInterface;
} => {
  return { documentCategories: document };
};

export default connect(mapStateToProps, {})(FileView);
